
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import event from '../../../js/Event.js'
import { mapState } from 'vuex'
import { AjaxHttp } from '../../../js/ajaxHttp'

export default {
name: 'createcomment',
components: {
XTextarea,
},
data() {
return {
content: '',
placeholder: "请填写",
}
},
computed: {
...mapState({
logMDetail: state => state.logDetail,
taskDetail: state => state.taskDetail,
}),
},
beforeDestroy() {
event.$off("saveDone");
},
mounted() {
event.$on('saveDone', function() {
this.saveInfos();
}.bind(this));

this.placeholder = '@' + this.$route.params.name;
},
methods: {
saveInfos() {
if (this.content === '') {
this.$vux.toast.text('请填写回复内容', 'top');
return;
}



var postParams = {};
var url = '';
if (this.$route.params.type === 'defalut') {
url = 'm=log&a=comment_add';
postParams['log_id'] = this.$route.params.id;
postParams['content'] = this.content;
} else if (this.$route.params.type === 'comment') {
url = 'm=log&a=comment_add';
postParams['talk_id'] = this.$route.params.id;
postParams['receiveid'] = this.$route.params.receiveid;
postParams['content'] = this.content;
} else if (this.$route.params.type === 'taskdefalut') {
url = 'm=task&a=comment_add';
postParams['task_id'] = this.$route.params.id;
postParams['content'] = this.content;
} else if (this.$route.params.type === 'taskcomment') {
url = 'm=task&a=comment_add';
postParams['talk_id'] = this.$route.params.id;
postParams['receiveid'] = this.$route.params.receiveid;
postParams['content'] = this.content;
}


AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
if (wself.$route.params.type === 'defalut' || wself.$route.params.type === 'comment') {
wself.logMDetail.refreshComment = true;
wself.$store.commit('updateLogDetail', wself.logMDetail);
} else if (wself.$route.params.type === 'taskdefalut' || wself.$route.params.type === 'taskcomment') {
wself.taskDetail.refresh = true;
wself.$store.commit('updateTaskDetail', wself.taskDetail);
}
wself.$vux.toast.text(res.info, 'top');
wself.$router.go(-1);
}
});
}
}
}




